html{height:100%;background:#24313c;color:#fff
}
body{margin:0;height:100%;background:#2b3b49;color:#fff;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
.wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;width:100%;height:100%;overflow:hidden
