}
.header>.brand>a:hover{color:#fff
}
.header>.toolbar{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-flex:1;-ms-flex:1;flex:1
}
.header>.toolbar>.list{display:-webkit-box;display:-ms-flexbox;display:flex;list-style:none;margin:0;padding:0
}
.header>.toolbar>.list>.item{position:relative
}
.header>.toolbar>.list>.item>a{display:block;padding:0 .625rem;color:#eceeef;font-size:.75rem;line-height:2rem
}
.header>.toolbar>.list>.item>a::before{margin-right:.3125rem
}
.header>.toolbar>.list>.item>a>img{max-width:1rem;max-height:1rem;margin-top:-.125rem;background:#3e4c59;border:0.0625rem solid #64707a;vertical-align:middle
}
.header>.toolbar>.list>.item>.list{display:none;list-style:none;position:absolute;z-index:99;min-width:5rem;margin:0;padding:.625rem;background:#24313c
}
.header>.toolbar>.list>.item>.list>.divider::before{display:block;height:.0625rem;margin:.125rem;background:#202c36;content:''
}
.header>.toolbar>.list>.item>.list>.item>a{display:block;padding:.125rem;color:#eceeef;font-size:.8125rem;white-space:nowrap
}
.header>.toolbar>.list>.item>.list>.item:hover>a{color:#00b9eb
}
.header>.toolbar>.list>.item:hover>a{background:#24313c;color:#00b9eb
}
.header>.toolbar>.list>.item:hover>.list{display:block
}
.header>.toolbar>.list:last-child>.item>.list{right:0;text-align:right
}
.sidebar{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;width:10rem;-webkit-transition:width .3s;transition:width .3s
}
.sidebar>.menu{-webkit-box-flex:1;-ms-flex:1;flex:1
}
.sidebar>.menu>.list{list-style:none;margin:0;padding:0
}
.sidebar>.menu>.list>.title{padding:.25rem .75rem;color:#7d8b92;font-size:.75rem;text-shadow:0 0 0.125rem #24313c
}
.sidebar>.menu>.list>.divider::before{display:block;height:.5rem;content:''
}
.sidebar>.menu>.list>.item{position:relative
}
.sidebar>.menu>.list>.item>a{display:block;color:#eceeef;font-size:.875rem;line-height:2.25rem;white-space:nowrap;overflow:hidden
}
.sidebar>.menu>.list>.item>a>i,.sidebar>.menu>.list>.item>a::before{padding:0 .625rem;font-size:1rem
}
.sidebar>.menu>.list>.item>.list{display:none;list-style:none;padding:.625rem .875rem
}
.sidebar>.menu>.list>.item>.list>.divider::before{display:block;height:.0625rem;margin:.125rem;background:#283643;content:''
}
.sidebar>.menu>.list>.item>.list>.item>a{display:block;padding:.125rem;font-size:.8125rem;color:#eceeef;white-space:nowrap;overflow:hidden
}
.sidebar>.menu>.list>.item>.list>.item.active a{color:#00a1cc
}
.sidebar>.menu>.list>.item>.list>.item:hover a{color:#00b9eb
}
.sidebar>.menu>.list>.item:hover>a{background:#24313c;color:#00b9eb
}
.sidebar>.menu>.list>.item:hover>.list{display:block;position:absolute;top:0;left:100%;z-index:99;min-width:6rem;background:#24313c;background:#1e2932
}
.sidebar>.menu>.list>.item:hover>.list::before{display:inline-block;position:absolute;top:0;left:-.875rem;margin:.6875rem 0;border:0.4375rem solid transparent;border-right-color:#1e2932;content:''
}
.sidebar>.menu>.list>.item.active>a{color:#fff;background:#337ab7
}
.sidebar>.menu>.list>.item.active>a::after{display:inline-block;position:absolute;right:0;margin:.6875rem 0;border:0.4375rem solid transparent;border-right-color:#f7f7f9;content:''
}
.sidebar>.menu>.list>.item.active>.list{display:block;position:static;background:#2f414f
}
