}
.sidebar>.footer>.toggle{display:inline-block;padding:0 .625rem;border-right:0.0625rem solid #283643;color:#7d8b92;font-size:1rem;-webkit-transition:-webkit-transform .3s;transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s
}
.sidebar>.footer>.toggle:hover{color:#eceeef
