.sidebar.collapse .title{display:none
}
.sidebar.collapse .menu{margin-top:.625rem
}
.sidebar.collapse .menu>.list>.item:not(:hover)>.list{display:none
}
.sidebar.collapse .menu>.list>.item.active>.list{position:absolute;background:#24313c
}
.sidebar.collapse .toggle{-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
